<template>
    <v-card class="mt-4">
        <v-card-title>
            <p class="my-3 text-h4 font-weight-bold">لیست کاربران</p>
            <v-spacer></v-spacer>

            <v-dialog v-model="dialog">
                <template v-slot:activator="{ on, attrs }">
                    <v-btn class="primary mx-3" v-on="on" v-bind="attrs"
                        >کاربر جدید
                        <v-icon class="mr-2">mdi-account-plus</v-icon>
                    </v-btn>
                </template>
                <v-card>
                    <v-card-text>فرم کاربر جدید</v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12" md="6" lg="4">
                                <v-text-field v-model="newUserForm.name" label="نام"></v-text-field>
                            </v-col>
                            <v-col cols="12" md="6" lg="4">
                                <v-text-field
                                    v-model="newUserForm.username"
                                    label="نام کاربری"
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" md="6" lg="4">
                                <v-text-field
                                    v-model="newUserForm.email"
                                    type="email"
                                    label="ایمیل"
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" md="6" lg="4">
                                <v-select
                                    v-model="newUserForm.gender"
                                    label="جنسیت"
                                    :items="[
                                        { genderName: 'مرد', value: 2 },
                                        { genderName: 'زن', value: 1 },
                                    ]"
                                    item-text="genderName"
                                    item-value="value"
                                ></v-select>
                            </v-col>
                            <v-spacer />
                            <v-col cols="12" md="6" lg="4">
                                <v-btn color="primary" block class="float-end" @click="saveUser"
                                    >ذخیره</v-btn
                                >
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card>
            </v-dialog>

            <v-text-field v-model="searchText" label="جستجو "></v-text-field>
        </v-card-title>
        <v-data-table
            :headers="headers"
            :items="users"
            :item-key="users.id"
            :search="search"
            :loading="loading"
            no-data-text="کاربری وجود ندارد"
            loading-text="در حال ارتباط با سرور"
            no-results-text="کاربری پیدا نشد"
            :footer-props="{
                'items-per-page-text': 'تعداد در صفحه:',
            }"
        >
            <template v-slot:header.gender="{ header }">
                <template v-if="header.text === 'جنسیت'">
                    <span>
                        جنسیت
                    </span>
                    <span class="blue--text">(مرد/زن)</span>
                </template>
                <template v-else>
                    {{ header.text }}
                </template>
            </template>
            <template v-slot:item.name="{ item }">
                {{ item.name.toUpperCase() }}
            </template>
            <template v-slot:item.gender="{ item }">
                <v-chip class="white--text" :color="item.gender === 1 ? 'pink' : 'blue'">
                    {{ item.gender === 1 ? 'زن' : 'مرد' }}
                </v-chip>
            </template>
            <template v-slot:item.actions="{ item }">
                <v-btn text @click="removeItem(item)">
                    <v-icon color="error">mdi-delete </v-icon>
                </v-btn>
            </template>
            <template v-slot:footer.page-text="{ pageStart, pageStop, itemsLength }">
                {{ `از ${pageStart} - ${pageStop}   کل ${itemsLength}` }}
            </template>
        </v-data-table>
    </v-card>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            headers: [
                { text: 'نام', value: 'name' },
                { text: 'نام کاربری', value: 'username' },
                { text: 'ایمیل', value: 'email' },
                { text: 'جنسیت', value: 'gender' },
                { text: 'عملیات', value: 'actions' },
            ],
            users: [],
            searchText: '',
            search: '',
            newUserForm: {
                name: '',
                username: '',
                email: '',
                gender: '',
            },
            dialog: false,
            loading: false,
        };
    }, watch: {
        searchText(newval, oldval) {
            if (newval === 'مرد') {
                this.search = '2';
            } else if (newval === 'زن') {
                this.search = '1';
            } else {
                this.search = newval;
            }
        },
    },
    methods: {
        saveUser() {
            const id = Math.random();
            this.users.push({ ...this.newUserForm, id });
            this.clear();
            this.dialog = false;
        },
        clear() {
            this.newUserForm = {
                name: '',
                username: '',
                email: '',
                gender: '',
            };
        },
        removeItem(item) {
            this.users = this.users.filter((user) => user !== item);
        },
    },
    created() {
        // console.log(this.$vuetify.breakpoint.name);
        // console.log(this.$vuetify.breakpoint.mdAndUp);
        // this.$vuetify.theme.dark = true;
        console.log({ ...this.$vuetify.breakpoint });
        this.loading = true
        axios.get('https://jsonplaceholder.typicode.com/users').then(({ data }) => {
            // console.log(data)
            this.users.push(...data);
        }).catch(err=>{
            console.log(err)
        }).finally(()=>{
            this.loading = false
        })
    },
}
</script>