<template>
    <v-row>
        <v-col cols="12" v-if="loading" >
            <v-icon>mdi-spinner</v-icon>
        </v-col>
        <v-col cols="12" md="6" lg="4" v-for="post in posts" :key="post.id">
            <v-card class="mx-auto" max-width="344" outlined>
                <v-list-item three-line>
                    <v-list-item-content>
                        <v-list-item-title class="text-h5 mb-1">
                            {{ post.title }}
                        </v-list-item-title>
                        <v-list-item>{{ post.body }}</v-list-item>
                    </v-list-item-content>

                    <v-list-item-avatar tile size="80" color="grey"></v-list-item-avatar>
                </v-list-item>

                <v-card-actions>
                    <v-btn outlined rounded text>
                        Button
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-col>
        <v-col cols="12">
            <div class="text-center">
                <v-pagination total-visible="6" v-model="currentpage" @input="next" :length="totalPage" circle></v-pagination>
            </div>
        </v-col>
    </v-row>
</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            posts: [],
            currentpage:1,
            totalPage:10,
            loading:true
        };
    },
    methods:{
        next(page){
            axios.get(`https://jsonplaceholder.typicode.com/posts?_page=${page}`).then((res) => {
            this.posts.splice(0,this.posts.length)
            this.posts.push(...res.data);
        });
        }
    },
    created() {
        // axios.get('https://jsonplaceholder.typicode.com/posts?_limit=10').then(({ data }) => {
        //     this.posts.push(...data);
        //     this.paginationLength = data.length / this.perpage;
        // });

        axios.get('https://jsonplaceholder.typicode.com/posts?_page=1').then((res) => {
            console.log(res.data);
            this.posts.push(...res.data);
        });
    },
};
</script>
